@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,400;1,700&family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500&family=Raleway:wght@100;200;300;600;700;800&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,500&display=swap');
/* font-family: 'Merriweather', serif; =>Georgia
font-family: 'Montserrat', sans-serif;
font-family: 'Raleway', sans-serif;
font-family: 'Roboto', sans-serif; */
body {
    margin: 0;
    box-sizing: border-box;
    letter-spacing: 0.005rem;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

.content h2 {
    font-size: 50px;
    font-weight: 600;
    line-height: 25px;
    color: #ffffff;
    margin-top: 4px;
}
.content .line {
    border-bottom: 2px solid #ff1d6a;
    width: 125px;
    margin-top: 24px;
    margin-bottom: 52px;
}
.content p {
    font-size: 20px;
    line-height: 25px;
    color: #ffffff;
    width: 578px;
}
.mask-cover {
    z-index: 0;
    position: absolute !important;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(24, 26, 31, 0.93);
}
.title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
