body {
    margin: 0;
    box-sizing: border-box;
    letter-spacing: 0.005rem;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

.content h2 {
    font-size: 50px;
    font-weight: 600;
    line-height: 25px;
    color: #ffffff;
    margin-top: 4px;
}
.content .line {
    border-bottom: 2px solid #ff1d6a;
    width: 125px;
    margin-top: 24px;
    margin-bottom: 52px;
}
.content p {
    font-size: 20px;
    line-height: 25px;
    color: #ffffff;
    width: 578px;
}
.mask-cover {
    z-index: 0;
    position: absolute !important;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(24, 26, 31, 0.93);
}
.title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

